.outter-footer {
  background-color: #495e57;
}

.footer {
  font-family: 'Karla', sans-serif;
  font-size: 1rem;
  max-width: 1125px;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start;
  gap: 15px;
  margin: 0 auto;
}

.footer ul {
  list-style-type: none;
  padding-left: 0;
  color: white;
}

.footer h3 {
  color: white;
}

.footer a {
  color: white;
  cursor: pointer;
}

.footer p {
  text-align: left;
  color: white;
}

#footer-img {
  border-radius: 30px;
  padding: 10px 30px;
  background-color: white;
}

@media screen and (max-width: 472px) {
  #cell_3 {
    width: 50%;
  }
}

@media screen and (min-width: 695px) and (max-width: 841px) {
  #cell_4 h3 {
    text-align: center;
  }
  
  #cell_4 li{
    display: inline-block;
    padding: 0 20px;
  }
}